
.dialog-mask{
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	background-color: rgba(0, 0, 0, .4);
}
.ani-dialog{
	position: absolute;
	width: 800px;
	left: 50%;
	top: 50%;
	-webkit-transform: translate(-50%, -50%);
	-moz-transform: translate(-50%, -50%);
	transform: translate(-50%, -50%);
}
.el-dialog__body-custom{
	height: 400px;
	padding: 10px 20px;
}
.fade-enter-active{
	transition: all .25s;
	transform-origin: left top;
}
.fade-leave-active{
	transition: all .25s;
	transform-origin: left top;
}
.fade-enter{
	opacity: 0;
	transform: scale(.3, .3);
}
.fade-leave-to{
	opacity: 0;
	transform: scale(.3, .3);
}



